.homeview {
  margin: 10px;
  padding: 10px;
  display: inline-block;
  text-align: center;
  background-color: bisque;
}
.p {
  text-shadow: 0 0 3px #FF0000, 0 0 5px #0000FF;
}
.homeview.img {
  animation: homeviewimg;
  animation-duration: 4s;
  animation-delay: 2s;
  position: relative;
}
@keyframes homeviewimg {
  0% {
    left: 0px;
    top: 0px;
  }
  50% {
    left: 200px;
    top: 200px;
  }
  100% {
    left: 0px;
    top: 0px;
  }
}
