.loginform{
    border-radius: 15px;
    opacity: 0.8;
    display: flex;
    flex-direction: column;
    /* text-align: center; */
    font-size: 20px;
    background-color: #590fcf;
    color: black;
    border: 1px solid black;
    
    width: 300px;
    padding: 30px;
    margin: auto;
    margin-top: 13%;
}

.loginform label{
    text-align: center;
}
.loginform input{
    margin-top: 2%;
    padding: 5px;
    width: 96%;

}

.loginform button{
    background-color:rgb(0, 255, 76);
}

